export default function setProductsAmountEnding(amount) {
  if (amount >= 11 && amount < 20) {
    return '';
  }
  switch (true) {
    case Number(String(amount).slice(-1)) === 0:
      return '';
    case Number(String(amount).slice(-1)) === 1:
      return '';
    case Number(String(amount).slice(-1)) <= 4:
      return '';
    case Number(String(amount).slice(-1)) > 4:
      return '';
  }
}
